	    if (res.from === 'button') {// 来自页面内分享按钮
	    }
	    return {
	      title: '来自黔诺康管理的分享',
	      path: '/pages/user/user?id=123'
	    }
	  },
	components: {
		customSwiper,
		NewTry,
